html, body, #root, #main {
    margin: 0px;
    padding: 0px;
    font-family: 'Noto Sans KR', '맑은 고딕', 'Malgun Gothic', sans-serif;
    height: 100%;
    animation: fadein 0.3s;
    -moz-animation: fadein 0.3s; /* Firefox */
    -webkit-animation: fadein 0.3s; /* Safari and Chrome */
    -o-animation: fadein 0.3s; /* Opera */
}

input:focus {
    outline:none;
}

a:link { 
    color: black; text-decoration: none;
}

a:visited { 
    color: black; text-decoration: none;
}

a:hover {
     color: black; text-decoration: none;
}
.flexContainer {
    padding: 2.5% 10%;
}

/*header*/
#header {
    width: 100%;
    display: flex;
    color: #303030;
    padding-bottom: 12%;
}

#header #pagename {
    font-size: 30PX;
    letter-spacing: 10PX;
    font-weight: 200;
    cursor: pointer;
}

/*content*/
#content {
    width: 100%;
    display: flex;
    height: 100%;
}

@keyframes fadein {
    from {
        opacity:0.5;
    }
    to {
        opacity:1;
    }
}
@-moz-keyframes fadein { /* Firefox */
    from {
        opacity:0.5;
    }
    to {
        opacity:1;
    }
}
@-webkit-keyframes fadein { /* Safari and Chrome */
    from {
        opacity:0.5;
    }
    to {
        opacity:1;
    }
}
@-o-keyframes fadein { /* Opera */
    from {
        opacity:0.5;
    }
    to {
        opacity: 1;
    }
}
