#activemenu {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
}

#activemenu #activemenu_header {
    display: flex;
    width: 100%;
}

#back_button {
    display: flex;
    cursor: pointer;
}

#back_button #back_arrow {
    display: flex;
    padding-top: 6px;
    cursor: pointer;
}

#back_button #back_arrow:after {
    content: '';
    display:inline-block;
    width: 0.8rem;
    height: 0.8rem;
    margin-left: 0.5rem;
    border-top: 0.1rem solid #333;
    border-right: 0.1rem solid #333;
    transform: rotate(225deg);

}

#back_button #arrow_name {
    display: flex;
    font-size: 16px;
    margin-left: 10px;
    padding-top: 1px;
    font-weight: 300;
    cursor: pointer;
}

#activemenu #activemenu_content {
    display: flex;
    width: 100%;
    height: 100%;
}