#menu {
    display: flex;
    width: 100%;
}

#menu .menu_box {
    font-size: 13px;
    width: 15%;
    margin-right: 6.5%;
    height: 100px;
    text-align: center;
    line-height: 100px;
    background: black;
    color: white;
    cursor: pointer;
    transition: all 0.3s;
} 

#menu .menu_box:last-child {
    margin-right: 0%;
}

#menu .menu_box:hover {
    opacity: 80%;
}