
#createfolder, #getfolderlist {
    display: flex;
    width: 100%;
    padding: 0px 10%;
    animation: fadein 0.3s;
    -moz-animation: fadein 0.3s; /* Firefox */
    -webkit-animation: fadein 0.3s; /* Safari and Chrome */
    -o-animation: fadein 0.3s; /* Opera */
}

.activemenu_content_box {
    margin: 4% 12%;
    display: flex;
    width: 100%;
}

#form, #result {
    width: 50%;
    padding-left: 10%;
    padding-right: 10%;
    
}

.form_header{
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 20px;

}

.form_input{
    display: inherit;
    margin-bottom: 15px;

}

.form_input_label {
    width: 40%;
    margin: 0;
    font-size: 13px;
    display: inline-block;
}

.form_input input {
    padding: 0;
    border: none;
    border-bottom: 2px solid black;
    font-size: 12px;
    cursor: default;
    width: 60%;
}

.form_input_btn {
    margin-top: 35px;
}
.form_input_btn div{
    background: #000;
    color: #fff;
    cursor: pointer;
    display: inline-block;
    font-size: 12px;
    font-weight: 300;
    padding: 5px 10px;
    transition: all .3s;
}

.form_input_btn div:hover{
    opacity: 80%;
}

.form_search {
    margin-left: 40%;
    width: 60%;
    height: auto;
    background-color: #fff;
    box-shadow: 0 12px 20px -12px rgb(0 0 0 / 35%);
    overflow: auto;
}

.form_search ul {
    list-style: none;
    padding: 0;
    margin-left: 10px;
    margin-right: 10px;
    font-size: 13px;
    margin-top: 15px;
    color: #828282;
}

.form_search li {
    margin-bottom: 5px;
}

.result_content {
    font-size: 12px;
    margin-bottom: 15px;
}

.result_content_btn {
    border-bottom: 2px solid #000;
    cursor: pointer;
    display: inline;
    font-size: 11px;
    font-weight: 600;
}

#getfolderlist {
    display: flex;

}

#getfolderlist .activemenu_content_box {
    flex-direction: column;
}

#getfolderlist #search {
    width: 100%;
}

#search input {
    border: none;
    border-bottom: 2px solid black;
    width: 100%;
    padding: 0;
    padding-bottom: 7px;
    font-size: 14px;
    
}

#getfolderlist #searchList {
    background-color: white;
    overflow: auto;
    max-height: 305px;
    box-shadow: 0 12px 20px -12px rgb(0 0 0 / 35%);
    padding: 20px;
    font-weight: 500;
}

#searchList ul {
    list-style: none;
    padding: 0;
    font-size: 13px;
    color: #828282;
    margin: 0;
    padding: 0;
}

#searchList li {
    margin-bottom: 8px;
}

#btns {
    text-align: right;
    margin-top: 15px;
}
#btns div {
    background: #000;
    color: #fff;
    cursor: pointer;
    display: inline-block;
    font-size: 12px;
    font-weight: 300;
    padding: 5px 10px;
    transition: all .3s;
}

#btns div:hover {
    opacity: 80%;
}

.click_disable {
    pointer-events : none;
}

@keyframes fadein {
    from {
        opacity:0.5;
    }
    to {
        opacity:1;
    }
}
@-moz-keyframes fadein { /* Firefox */
    from {
        opacity:0.5;
    }
    to {
        opacity:1;
    }
}
@-webkit-keyframes fadein { /* Safari and Chrome */
    from {
        opacity:0.5;
    }
    to {
        opacity:1;
    }
}
@-o-keyframes fadein { /* Opera */
    from {
        opacity:0.5;
    }
    to {
        opacity: 1;
    }
}